<template>
  <div class="home">
    <Counter :value="counterValue" :tasbeeh="tasbeeh"/>
    <!-- <section id="content" style="background:transparent">
      <div style="background:transparent">
        <input type="range" min="0" max="100" step="0.1" v-model="counterValue" />
      </div>
    </section> -->
  </div>
</template>

<script>
// @ is an alias to /src
import Counter from '@/components/Counter.vue'

export default {
  name: 'home',
  components: {
    Counter,
  },
  data: function(){
    return {
      counterValue: 0,
      tasbeeh: {
        hadeeth: '"يقول النبي عليه الصلاة والسلام: "من قال حين يصبح وحين يمسي: سبحان الله وبحمده مائة مرة غفرت خطاياه وإن كانت مثل زبد البحر',
        text: "سبحان الله وبحمده",
        times:100
      }
    }
  },
  methods: {
    animate: function(direction = 1){
      setTimeout(() => {
        this.counterValue = this.counterValue + direction
        if(this.counterValue == 100 || this.counterValue == 0)
          direction = -direction
        this.animate(direction)
        
      }, 10);
    }
  },
}
</script>

