import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    width: null,
    height: null,
  },
  mutations: {
    "resize": (state, {
      innerWidth, innerHeight
    }) => {
      state.width = innerWidth
      state.height = innerHeight
    }
  },
  actions: {

  }
})
